import Heading from '../layouts/Heading';
import proj1 from '../../images/projectImp/react-signup.png';
import proj2 from '../../images/projectImp/react-portfolio.png';
import proj3 from '../../images/projects/has-website-react.png';
import InterestJson from './Json/InterestJson';
import programming from '../../images/animated-icons/programming.gif';

// cricket

const Projects = () => {
    return (
        <section className="technology educations projects">
            <Heading
                heading="Projects"
            />
            <div className='skills-highlights mt-4 d-flex flex-wrap justify-content-between'>
                <a href='https://pekskumar.github.io/Prakash-SignUpFlow/' className="item p-4 mb-4 d-flex  justify-content-between align-items-center">
                    <div className="elogo">
                        <img src={proj1} alt="" />
                    </div>
                    <div className="econtent ps-4">
                        <ul className='pro-lang'>
                            <li>React Js</li> 
                            <li>HTML5</li> 
                            <li>SCSS</li> 
                            <li>Javascript</li> 
                            <li>jQuery</li> 
                        </ul>
                        <a className="custom-btn new" target="_blank" href="https://pekskumar.github.io/Prakash-SignUpFlow/">Visit Site </a>
                    </div>
                </a>
                <a href='https://pekskumar.github.io/Portfolio-React/' className="item p-4 mb-4 d-flex  justify-content-between align-items-center">
                    <div className="elogo">
                        <img src={proj2} alt="" />
                    </div>
                    <div className="econtent ps-4">
                        <ul className='pro-lang'>
                        <li>React Js</li> 
                            <li>HTML5</li> 
                            <li>SCSS</li> 
                            <li>Javascript</li> 
                            <li>jQuery</li>  
                        </ul>
                        <a className="custom-btn new" target="_blank" href="https://pekskumar.github.io/Portfolio-React/">Visit Site </a>
                    </div>
                </a>
                <a href='https://pekskumar.github.io/Has-Websites-Reactjs-js/' className="item p-4 mb-4 d-flex  justify-content-between align-items-center">
                    <div className="elogo">
                        <img src={proj3} alt="" />
                    </div>
                    <div className="econtent ps-4">
                    <ul className='pro-lang'>
                            <li>React Js</li> 
                            <li>HTML5</li> 
                            <li>SCSS</li> 
                            <li>Javascript</li> 
                            <li>jQuery</li> 
                        </ul>
                        <a className="custom-btn new" target="_blank" href="https://pekskumar.github.io/Has-Websites-Reactjs-js/">Visit Site </a>
                    </div>
                </a>
            </div>

            <Heading
                heading="Our Speciality Interest"
            />
            <div className='skills-highlights mt-4 d-flex flex-wrap extensions interest'>
                {
                    InterestJson.map((val) => (
                        <div className="item item-2 p-4 mb-4 d-flex  justify-content-between align-items-center">
                            <div className="elogo">
                                <img src={val.Icon} alt="" />
                            </div>
                            <div className="econtent ps-4">
                                <h5>{val.title}</h5>
                            </div>
                        </div>
                    ))
                }
            </div>
        </section>
    )
}
export default Projects;