import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Heading from '../layouts/Heading';
import SkillsJson from './Json/SkillsJson';
import SkillsHighlights from "./Json/SkillsHighlights";

const Technology = () => {

  var settings = {
    slidesToShow: 5,
    slidesToScroll: 5,
    arrows: false,
    dots: true,
    infinite: true,
    responsive: [
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 4,
          adaptiveHeight: true,
        },
      },
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          adaptiveHeight: true,
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          adaptiveHeight: true,
        },
      },
      {
        breakpoint: 480,
        settings: {
          dots: false,
          slidesToShow: 1,
          slidesToScroll: 1,
          adaptiveHeight: true,
        },
      },
    ],
  };

  return (
    <section className='technology'>
      <Heading
        heading="Technologies"
      />
      <Slider {...settings} className='skills mb-5'>
        {
          SkillsJson.map((val) => {
            return (
              <div className='item p-4'>
                <img src={val.skillIcon} alt="skill-Icon" />
                <h5>{val.skillTitle}</h5>
              </div>
            )
          })
        }
      </Slider>
      <Heading
        heading="Speciality"
      />
      <div className='skills-highlights mt-4 d-flex flex-wrap justify-content-between skill-high'>
        {
          SkillsHighlights.map((val) => {
            return (
              <div className="item p-4 mb-4 text-center">
                <img src={val.ShIcon} alt="Sh" />
                <h5>{val.ShTitle}</h5>
              </div>
            )
          })
        }
      </div>
    </section>
  )
}

export default Technology;
