import React from 'react'
import Heading from '../layouts/Heading';
import { Formik , Form , Field } from 'formik';

const Signup = () => {
    
    const formInitialSchema = {
        name:''
    }

    const handleFormSubmit = (values) => {
        console.log("Submitted Values",values)
    }


  return (
    <section id='testimonial-area' className='signup-page text-center'>
        <Heading
            heading="Please Register your Details Here"
        />
        <Formik initialValues={formInitialSchema} onSubmit={(values => handleFormSubmit(values))}> 
        <form className='signup-form p-3'>
            <div className='form-group text-start'>
                <label for="name" class="form-label">First Name</label>
                <Field 
                className='form-control'
                id='name'
                type='text'
                placeholder='Enter Your Name Here'
                name='name' />
                <button className='button custom-btn' type='submit'>Submit</button>
            </div>
            

            
        </form>
        </Formik>
    </section>
  )
}

export default Signup;
