import React, { useState } from 'react';
import Heading from '../layouts/Heading';
import ExpJason from './Json/ExpJson';
import ProfileImg from '../../images/profileImg.jpg';
import 'react-responsive-modal/styles.css';
import { Modal } from 'react-responsive-modal';
import { NavLink } from 'react-router-dom';


const About = () => {

    const [content, setContent] = useState(null);

  const [open, setOpen] = useState(false);
  const viewParmar = (data) => {
    setContent(data);
    console.log("data", data)
  }

  content?.eData[0] && Object.keys(content?.eData[0])?.map(function (key) {
    console.log("key", key)
    return <li value={key}>{content?.eData[0][key]}</li>
  })
  console.log("content", content)

    return (
        <>
        <section className="dashboard-one">
        <Heading 
        heading = "My Profile"
        />
      <div className='about-block mb-5'>
        {/* <div className='about-block-title mb-3'>
          <h1>About Acquaint</h1>
        </div> */}
        <div className='about-block-inner d-flex flex-wrap justify-content-between align-items-center'>
          <div className='about-img'>
            <img className='rounded-circle' src={ProfileImg} alt="" />
          </div>
          <div className='about-content'>
            <p className='mb-4'>I’ve been working from 2 year as a
Frontend Developer. During that time
I’ve been trained software platforms
and systems. I have experience in
React js, HTML5, CSS3, SCSS, Jquery,
Javascript and Bootstrap. I am
Passionate about implementing and
launching new projects. Looking to
start the career as Full-Stack Frontend
Developer with a reputed firm driven
by technology.
</p>
            <div className='about-content-btn'>
              <a className='custom-btn me-4' href="tel:8866808798">Contact me</a>
              <NavLink className='custom-btn' to='/contact'>Hire me</NavLink>
            </div>
          </div>
        </div>
      </div>
      <div>
      <Heading 
        heading = "Experiences"
        />
      <div className='exp-block d-flex flex-wrap justify-content-between'>
        {
          ExpJason.map((val, i) => (
            <div className='item about-block text-center p-4' key={i}>
              {/* <img className='mb-3' src={val.eLogo} alt="" /> */}
              <h4>{val.eName}</h4>
              <h5>{val.ePost}</h5>              
              <p>{val.eYear}</p>
              <ul className='left-arrow'>
                {val.eData[0] && Object.keys(val.eData[0])?.map(function (key) {
                  return <li key={key} value={key}>{val.eData[0][key]}</li>
                })}
              </ul>
              <a onClick={() => {
                setOpen(true)
                viewParmar(val)
              }} className="button custom-btn">View More</a>
            </div>
          ))
        }
      </div>
      </div>



      <Modal
        open={open}
        onClose={() => setOpen(false)}
        center
        classNames={{
          overlay: 'customOverlay',
          modal: 'customModal',
        }}
      >


        <div className='item about-block text-center p-4'>
          {/* <img className='mb-3' src={content?.eLogo} alt="" /> */}
          <h4>{content?.eName}</h4>
          <h5>{content?.ePost}</h5>              
          <p>{content?.eYear}</p>
          <ul className='left-arrow'>
            {content?.eData[0] && Object.keys(content?.eData[0])?.map(function (key) {
              return <li key={key} value={key}>{content?.eData[0][key]}</li>
            })}
          </ul>
        </div>
      </Modal>

    </section>
        </>
    )
}
export default About;